import store from '@/store/index.js'
import http, { clear } from './http.js'
import JsNativeApi from '@/utils/js-native-api.js'
import { clearMails } from '@/utils/common.js'
import { Toast } from 'vant'

let timer = -1

function info () {
  return store.state.authInfo
}

export function save (data) {
  let authInfo = info()
  if (authInfo && data && data.type < authInfo.type) {
    return
  }
  store.commit('authInfo', data)
  retry(-1)
  if (!data || data.type === 0) {
    store.commit('userInfo', null)
  } else {
    userFetching = false
    getUserInfo()
  }
}

function retry (time = 0, cb) {
  clearTimeout(timer)
  let authInfo = info()
  if (authInfo && authInfo.type === 0) {
    // 过期时间大于1小时则过期前一小时重新获取
    // 否则2/3时获取
    if (time === -1) {
      time = authInfo.tokenExpire - Date.now()
      if (time < 3600000) {
        time = Math.floor(time / 3 * 2)
      } else {
        time -= 3600000
      }
    }
    timer = setTimeout(() => authNomral(cb), time)
  }
}

let fetching = false
export function authNomral (cb) {
  let authInfo = info()
  if (fetching || authInfo && authInfo.type > 0) {
    return
  }

  fetching = true
  http.post('/api/common/user/token', {
    deviceId: 'app-device'
  }, {
    globals: true,
    auth: false
  }).then(data => {
    save({
      type: 0,
      token: data.token,
      tokenExpire: data.tokenExpire
    })
    cb && cb()
  }, () => {
    retry(2000, cb)
  }).finally(() => fetching = false)
}

export function isExpired (skip = false) {
  let authInfo = info()
  if (!authInfo) {
    console.log('Unauthorized')
    if (!skip) {
      logout()
    }
    return true
  }

  let expire = authInfo.tokenExpire
  if (expire <= Date.now()) {
    console.log('Token is expired')
    if (skip) {
      save(null)
    } else {
      logout()
    }
    return true
  }

  // 提前10分钟获取
  if (expire <= Date.now() + 600000) {
    console.log('Token will expire in 10 minutes')
    if (!skip) {
      authNomral()
    }
  }

  return false
}

let userFetching = false
export function getUserInfo () {
  if (userFetching) {
    return
  }
  let authInfo = info()
  if (authInfo && authInfo.type > 1) {
    userFetching = false
    http.post('/api/common/user/info', null, {
      globals: true,
      login: true
    }).then(data => {
      let cinfo = info()
      if (cinfo.type === authInfo.type && cinfo.userId === authInfo.userId) {
        userFetching = false
        store.commit('userInfo', data)
      }
    }, err => {
      let cinfo = info()
      if (cinfo.type === authInfo.type && cinfo.userId === authInfo.userId && err.code !== -1016) {
        setTimeout(getUserInfo, 2000)
      }
    })
  }
}

export function logout (cb) {
  Toast.loading()

  clear()
  clearMails()

  save(null)
  authNomral(() => {
    try {
      cb && cb()
    } catch (e) {
      console.log(e)
    }
    Toast.clear()
  })
}

export function auth () {
  return new Promise(resolve => {
    let authInfo = window.localStorage.getItem('authInfo') || null
    if (authInfo) {
      authInfo = JSON.parse(authInfo)
      if (!authInfo.tokenExpire || authInfo.tokenExpire <= Date.now()) {
        console.log('Clear authorization')
        authInfo = null
        window.localStorage.removeItem('authInfo')
      }
    }
    if (authInfo) {
      save(authInfo)
      resolve()
    } else {
      authNomral(resolve)
    }
  })
}
