<template>
  <div class="l-page">
    <v-nav :title="$t('ai.title')"/>
    <div class="l-page__content l-flex--column">
      <div class="l-flex--row has-shadow--short l-flex__none">
        <router-link
          tag="div"
          class="o-photograph u-relative l-flex__none"
          :to="{ name: 'photograph' }"
        >
          <van-icon name="photograph"/>
          <div class="o-help" :class="{ 'is-hide': hide }">{{ $t('ai.help') }}</div>
        </router-link>
        <div class="c-types l-hlist l-flex--row l-flex__fill">
          <div class="l-hlist__wrapper">
            <div
              v-for="(option, oindex) in list"
              :key="oindex"
              class="c-types__item o-option"
              :class="{ 'is-selected': oindex === index }"
              @click="index = oindex"
            >
              {{ option.name }}
            </div>
          </div>
        </div>
      </div>
      <div class="c-list has-padding--around u-scroll-y l-flex__fill">
        <animal
          v-for="(item, index) in list"
          :key="index"
          class="c-list__item"
          :entity="item"
          large
        />
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant'
import Animal from '@/components/map/Animal.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    Animal
  },
  data () {
    return {
      hide: false,
      index: 0,
      list: null
    }
  },
  created () {
    this.$http.post('/api/common/entity/list/animal', null).then(data => {
      this.list = data.data
    })
    let hasShow = window.localStorage.getItem('ai')
    if (hasShow && hasShow === 'triggered') {
      this.hide = true
      this.$timer = -1
    } else {
      window.localStorage.setItem('ai', 'triggered')
      this.$timer = setTimeout(() => this.hide = true, 3000)
    }
  },
  beforeDestroy () {
    clearTimeout(this.$timer)
  }
}
</script>

<style lang="scss" scoped>
.o-photograph {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 106px;
  height: 106px;
  color: $color-blue;
}

.o-help {
  position: absolute;
  left: 50%;
  bottom: 10px;
  padding: 24px 32px;
  font-size: 28px;
  color: $color-white;
  line-height: 34px;
  white-space: nowrap;
  transform: translate(-32px, 100%);
  border-radius: 16px;
  background-color: rgba($color-black, .7);
  pointer-events: none;

  &.is-hide {
    display: none;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 32px;
    transform: translate(-50%, -100%);
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 14px solid rgba($color-black, .7);
  }
}

.c-types {
  height: 106px;

  .l-hlist__wrapper {
    padding: 0 40px 0 20px;
  }

  &__item {
    & + & {
      margin-left: 14px;
    }
  }
}

.c-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;

  &::after {
    content: '';
    display: inline-block;
    flex: 1 0 0;
    min-width: 40%;
    max-width: 50%;
  }

  &__item {
    flex: 1 0 0;
    min-width: 40%;
    max-width: 50%;

    &:nth-child(n + 3) {
      margin-top: 40px;
    }

    &:nth-child(odd) {
      margin-right: 32px;
    }
  }
}
</style>
